$(() =>{
    const $accordion = $(".js-accordion");

    if (!$accordion.length) return;

    initAccordion()

    $accordion.on("click", ".accordion__head", e=> {
        const target = $(e.currentTarget)
        const type = target.closest(".js-accordion").data("type")
        const parent = target.parent()
        const body = target.next()

        if (type === "toggle") {
            parent.toggleClass("accordion__item-show")
            parent.hasClass("accordion__item_show") ?
                body.css("max-height", body[0].scrollHeight) : body.css("max-height", 0)
        } else {
            parent.addClass("accordion__item_show").siblings().removeClass("accordion__item_show")
            body.css("max-height", body[0].scrollHeight).parent().siblings().find(".accordion__body").css("max-height", 0)
        }
    })

    function initAccordion() {
        $accordion.each((_, wrap) => {
            const that = $(wrap)
            const activeItem = +that.data("active-item") || 0;
            that.find(".accordion__item").eq(activeItem).addClass("accordion__item_show")
            const body = that.find(".accordion__body").eq(activeItem)
            body.css("max-height", body[0].scrollHeight)
        })
    }
}) 