export class MyTimer {
    constructor( $timer, startTime) {
        this.$timer = $timer;
        this.startTime = startTime;
        this.mothNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        this.updateTimer = this.updateTimer.bind(this);
    }
 
    mount() {
        this.timerInterval = setInterval(() => this.updateTimer(), 1000);
    }
    addZero(count) {
        return count < 10 ? "0" + count : count
    }

    stopTimer(days, time) {
        return days < 0 ? "00" : time
    }

    tomorrowDate(now) {
        const month = this.mothNames[now.getMonth()];
        const date = now.getDate() + 1;
        const year = now.getFullYear();

        const tomorrowDay = new Date(`${month} ${date}, ${year}`)
        return tomorrowDay
    }

    isEmpty(obj) {
        return !obj || Object.keys(obj).length === 0
    }

    updateTimer() {
        const now = new Date();
        const tomorrow = this.tomorrowDate(now)
        const stater = this.isEmpty(this.startTime) ? 
            tomorrow : ` ${this.mothNames[this.startTime.month - 1]} ${this.startTime.date}, ${now.getFullYear()}`;

        const future = Date.parse(stater);

        const diff = future - now;

        const days = Math.floor(diff / (1000 * 60 * 60 * 24))
        const hours = Math.floor(diff / (1000 * 60 * 60))
        const mins = Math.floor(diff / (1000 * 60))
        const secs = Math.floor(diff / 1000)

        const timer = {
            d: this.stopTimer(days, this.addZero(days)),
            h: this.stopTimer(days, this.addZero(hours - days * 24)),
            m: this.stopTimer(days, this.addZero(mins - hours * 60)),
            s: this.stopTimer(days, this.addZero(secs - mins * 60)),
        }


        this.$timer.innerHTML = this.template(timer)

        if(days < 0) {
            clearInterval(this.timerInterval)
        }
    }

    template({ d, h, m, s}) {
        return `
            <div class="timer-wrap">
                <div class="timer-item">
                    <div class="timer-num">${d}</div>
                    <div class="timer-text">дней</div>
                </div>
                <div class="timer-item">
                    <div class="timer-num">${h}</div>
                    <div class="timer-text">часов</div>
                </div>
                <div class="timer-item">
                    <div class="timer-num">${m}</div>
                    <div class="timer-text">минут</div>
                </div>
                <div class="timer-item">
                    <div class="timer-num">${s}</div>
                    <div class="timer-text">секунд</div>
                </div>
            </div>
        `
    }
}