import { MyTimer } from "./timer";
$(()=> {
    const $color = $('.js-product-color');

    if($color.length > 0) {
        $color.on("click", "li[data-color]", e=> {
            const that = $(e.currentTarget)
            const { model, format, color } = getData(that)
            const renderImage = imageConverter(that, model)
            that.addClass("active").siblings().removeClass("active");
            renderImage(`${color}.${format}`)
        })

        function getData(that) {
            const { model, format } = that.parent()[0].dataset
            const { color } = that[0].dataset
            return {
                model,
                format,
                color,
            }
        }

        function imageConverter(that, model) {
            const image = that.parents('.product').find(`.js-images[data-model="${model}"] img`)
            const getImgPath = image.attr("src")
            const currentPath = getImgPath.substring(0, getImgPath.lastIndexOf("/") + 1);

            return newPath=> image.attr("src", `${currentPath}${newPath}`)
        }
    }
})

const $timer = $(".js-timer");